;EACA,oCAAA;UAAA,4BAAA;CD48BD;ACz8BD;;EAEE,WAAA;EACA,qCAAA;UAAA,6BAAA;CD28BD;ACx8BD;;;;EAIE,yBAAA;EAAA,iBAAA;EACA,mBAAA;EACA,OAAA;CD08BD;ACx8BD;EACE,UAAA;EACA,WAAA;CD08BD;ACx8BD;;EAEE,UAAA;EACA,WAAA;EACA,aAAA;EACA,YAAA;EACA,mBAAA;EACA,iBAAA;EACA,oBAAA;EACA,mCAAA;EACA,mBAAA;CD08BD;ACp9BD;;EAYI,SAAA;CD48BH;ACx9BD;;EAeI,sBAAA;CD68BH;AC59BD;;EAkBI,uBAAA;EACA,2DAAA;EAAA,mDAAA;EACA,YAAA;EACA,aAAA;EAEA,mBAAA;EACA,oCAAA;UAAA,4BAAA;EACA,0BAAA;UAAA,kBAAA;EACA,+BAAA;UAAA,uBAAA;CD68BH;ACv+BD;;EA6BI,gCAAA;CD88BH;AC3+BD;;EAgCI,oDAAA;EACA,oCAAA;EACA,mCAAA;EACA,aAAA;EACA,mBAAA;CD+8BH","file":"App.vue","sourcesContent":["@import \"theme.less\";\nul, li{\n  list-style-type: none;\n}\n.van-swipe__indicator{\n  width: 10px;\n  height: 10px;\n}\n.mescroll-upwarp{\n  margin-bottom: 60px;\n}\n.mescroll{\n  height: 100% !important;\n  overflow: hidden;\n  overflow: auto;\n}\n::-webkit-scrollbar {\n  display: none;\n}\n/* 首页 */\n.home{\n  height: 100%;\n  position: relative;\n  overflow: hidden;\n  // .sell{\n  //   height: 100%;\n  //   overflow: auto;\n  // }\n  .my-swipe{\n    width: 100%;\n    height: 150px;\n  }\n  .home-headImg{\n    width: 100%;\n    height: 100%;\n  }\n  .shop-infor{\n    height: 100px;\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: space-between;\n    position: relative;\n    box-sizing: border-box;\n    padding: 0px 10px 0px 15px;\n  }\n  .shop-headimg{\n    width: 65px;\n    height: 65px;\n    border-radius: 50px;\n  }\n  .infocard-bgk{\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: -9999;\n  }\n  .shopinfor-content{\n    width: 53%;\n  }\n  .shopinfor-content p{\n    width: 100%;\n    overflow: hidden;\n    text-align:left;\n    text-align: justify;\n  }\n  .shop-name{\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n  .shopinfor-content p i{\n    margin-right: 5px;\n    color: @blueColor;\n  }\n  .phone{\n    white-space: nowrap;\n    margin: 5px 0px 3px 0px;\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n  }\n  .address{\n    word-break: break-all;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-box-orient: vertical;\n    -webkit-line-clamp: 2;\n    color: @greyColor;\n    font-size: 13px;\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n  }\n  .shop-coupon{\n    width: 23%;\n    height: 90%;\n    position: relative;\n    box-sizing: border-box;\n    padding: 7px;\n    margin-top: 5px;\n  }\n  .shop-coupon p{\n    text-align: center;\n    width: 100%;\n    white-space: nowrap;\n    overflow: hidden;\n  }\n  .couponimg{\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: -999;\n  }\n  .money-p{\n    color: @redColor;\n    margin-top: 5px;\n  }\n  .money-p .symbol{\n    \n  }\n  .money-p .money{\n    font-size: 30px;\n  }\n  .coupon-text{\n    font-size: 10px;\n  }\n  .lingqu-but{\n    width: 80% !important;\n    height: 15px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 10px;\n    background-color: @redColor;\n    color: @whiteColor;\n    margin:5px auto auto auto;\n  }\n  .tabs{\n    width: 100%;\n  }\n  .tabs ul{\n    width: 100%;\n    display: flex;\n   flex-wrap:wrap;\n   box-sizing: border-box;\n   padding-left: 12px;\n   margin-top: 15px;\n  //  margin-bottom: 80px;\n  }\n  .tabs ul li{\n   width: 30%;\n   height: 145px;\n   border-radius: 5px;\n   margin-right: 12px;\n   box-shadow:0px 0px 8px 5px @eeColor;\n   margin-bottom: 15px;\n   position: relative;\n  }\n  .commodity-img{\n    width: 100%;\n    height: 80px;\n    border-radius: 5px 5px 0px 0px;\n  }\n  .tabs ul li p{\n    width: 100%;\n    overflow: hidden;\n    text-align:left;\n    text-align: justify;\n    box-sizing: border-box;\n  }\n  .commodity-name{\n    word-break: break-all;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-box-orient: vertical;\n    -webkit-line-clamp: 2;\n    padding: 0px 5px;\n    font-size: 13px;\n  }\n  .price-addicon{\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: space-between;\n    padding: 0px 5px;\n    position: absolute;\n    bottom: 3px;\n  }\n  .price{\n    color: @redColor;\n  }\n  .addicon{\n    color:@blueColor;\n    font-size: 18px;\n  }\n}\n/* 商品详情 */\n.product-details{\n  height: 100%;\n  overflow: hidden;\n  position: relative;\n  .details-img{\n    width: 100%;\n    height: 200px;\n  }\n  .van-stepper__plus{\n    background-color: @blueColor;\n  }\n  .van-stepper__minus{\n    border: 1px solid @blueColor;\n    color: @blueColor;\n  }\n  .details-content{\n    width: 92%;\n    margin: auto;\n  }\n  .details-content p{\n    width: 100%;\n    text-align:left;\n    text-align: justify;\n  }\n  .details-name{}\n  .monthly-sales{\n    color: @greyColor;\n    margin: 5px 0px;\n    white-space: nowrap;\n    overflow: hidden;\n  }\n  .price-stepper{\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: space-between;\n  }\n  .price{\n    width: 60%;\n    white-space: nowrap;\n    overflow: hidden;\n    color: @redColor;\n    white-space: nowrap;\n    overflow: hidden;\n    .size{\n      font-size: 25px;\n    }\n  }\n}\n/* 页脚按钮 */\n.footer-but{\n  width: 100%;\n  position: absolute;\n  bottom: 0;\n  height: 50px;\n  box-shadow: 0px 0px 8px 2px @eeColor;\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  box-sizing: border-box;\n  padding: 0px 15px;\n  justify-content: space-between;\n  .total-price{\n    width: 50%;\n    color: @redColor;\n  }\n  .gongji{\n    color: @greyColor;\n  }\n  .price{\n    font-size: 25px;\n  }\n  .but{\n    width: 45%;\n    display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  }\n  .but .add-cart{\n    width: 60%;\n    height: 30px;\n    line-height: 30px;\n    text-align: center;\n    background-image: linear-gradient(to right, @orangeColor,@deepOrangeColor);\n    border-radius: 50px 0px 0px 50px;\n    color: @whiteColor;\n  }\n  .settlement{\n    width: 40%;\n    height: 30px;\n    line-height: 30px;\n    text-align: center;\n    border-radius: 0px 50px 50px 0px;\n    color: @whiteColor;\n    background-color: @redColor;\n  }\n}\n\n.footer{\n  width: 100%;\n  position: absolute;\n  bottom: 0;\n  height: 50px;\n  box-shadow: 0px 0px 8px 2px @eeColor;\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  background-color: @whiteColor;\n  .left{\n    width: 65%;\n    box-sizing: border-box;\n    padding: 0px 15px;\n    white-space: nowrap;\n    overflow: hidden;\n  }\n  .right{\n    width:40%;\n    height: 100%;\n    line-height: 50px;\n    text-align: center;\n    background-color: @deeColor;\n    color: @whiteColor;\n    font-size: 17px;\n  }\n  .gongji{\n    color: @greyColor;\n  }\n  .jiage{\n    color: @redColor;\n    b{\n      font-size: 25px;\n      font-weight: 500;\n    }\n  }\n}\n/* 确认订单 */\n.order-confirm{\n  height: 100%;\n  overflow: hidden;\n  .van-tabs{\n    height: 100%;\n    overflow: hidden;\n    overflow: auto;\n  }\n  .van-tabs__line{\n    width: 100px;\n    border-color: @blueColor;\n    background-color:@blueColor;\n  }\n  .van-tab--active{\n    color: @blueColor;\n  }\n  .van-tabs__wrap{\n    border-bottom: 1px solid @eeColor;\n  }\n  .address{\n    margin-top: 15px;\n  }\n  .address p{\n    box-sizing: border-box;\n    padding: 0px 15px;\n    width: 100%;\n  }\n  .xingming-dianhua{\n    white-space: nowrap;\n    overflow: hidden;\n    margin-bottom: 5px;\n    display: flex;\n    align-items: center;\n    flex-wrap: nowrap;\n  }\n  .recipients{\n    max-width: 60%;\n    margin-right: 10px;\n    overflow: hidden;\n  }\n  .phone{\n    max-width:35%;\n    white-space: nowrap;\n    overflow: hidden;\n  }\n  .address-text{\n    display: flex;\n    align-items: center;\n    flex-wrap: nowrap;\n    justify-content: space-between;\n  }\n  .text{\n    width: 80%;\n    margin-right: 10px;\n    color: @greyColor;\n  }\n  .dizhi{\n    font-size: 30px;\n    color: @blueColor;\n  }\n  .gengduo{\n    color: @greyColor01;\n  }\n  .xu-xian{\n    width: 100%;\n    height: 1px;\n    margin-top: 15px;\n    background: repeating-linear-gradient(-45deg,#ff6c6c 0,#ff6c6c 20%,transparent 0,transparent 25%,#1989fa 0,#1989fa 45%,transparent 0,transparent 50%);\n    background-size: 100px;\n    content: '';\n  }\n  .order-list ul{\n    margin-bottom: 110px;\n  }\n  .order-list ul li{\n    box-shadow: 0px 0px 8px 2px @eeColor;\n    margin-top: 15px;\n  }\n  .title-name{\n    box-sizing: border-box;\n    padding:10px 15px;\n    border-bottom:1px solid @eeColor ;\n  }\n  .shangpin-content{\n    display: flex;\n    flex-wrap: nowrap;\n    justify-content: space-between;\n    box-sizing: border-box;\n    padding: 10px 15px;\n  }\n  .shangpin-content .shangpin-img{\n    width: 65px;\n    height: 65px;\n  }\n  .con{\n    width: 78%;\n    // margin-right: 18px;\n  }\n  .con-p{\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: space-between;\n  }\n  .con-p .name{\n    width: 75%;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n  .con-p .number{\n    width: 20%;\n    text-align: right;\n    color: @greyColor;\n  }\n  .jiage{\n    width: 100%;\n    white-space: nowrap;\n    overflow: hidden;\n    color: @redColor;\n    margin-top: 20px;\n  }\n  .jiage-numder{\n    font-size: 25px;\n  }\n  .wudianzhi{\n    height: 40px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    color: @whiteColor;\n    background-color: @blueColor;\n    font-size: 16px;\n    margin-top: 15px;\n  }\n  .addicon{\n    font-size: 20px;\n  }\n}\n\n/* 支付成功 */\n.pay-success{\n  height: 100%;\n  overflow: hidden;\n  .top-img{\n    height: 32%;\n    position: relative;\n  }\n  .paySuccess{\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: -9999;\n  }\n 